<template>
    <h1>GitHub</h1>
    <input type="text" placeholder="Repository Name:" v-model="repo" required/>
    <br />
    <br />
    <br />
    <input type="text" placeholder="Owner:" v-model="owner" required/>
    <br />
    <br />
    <br />
    <input type="text" placeholder="Issue Title:" v-model="title" required/>
    <br />
    <br />
    <br />
    <input type="text" placeholder="Content:" v-model="body"/>
    <br />
    <br />
    <br />
    <br />
    <button v-on:click="getGithub">Confirm</button>
</template>

<script>    
// import axios from "@/api/axios";
// var validator = require("email-validator");
const qs = require('qs');

export default {
    name: "GithubREA",
    data() {
        return {
            repo: null,
            owner : null,
            title: null,
            body: null
        }
    },
    methods: {
        getGithub(e) {
            e.preventDefault();
            console.log(this.repo, this.owner, this.title, this.body)
            if (this.repo != null && this.title != null && this.owner != null && this.body != null) {
                    let url = 'http://localhost:8080/ar-user/'
                    let mes = {
                        "repo" : this.repo,
                        "owner" : this.owner,
                        "title" : this.title,
                        "body" : this.body
                    }
                    let res = {
                        "action": {
                            "id" : localStorage.actionId,
                            "content" : localStorage.action
                        },
                        "reaction": [ 
                            {
                                "id" : "636277770bf8e24d4f8155ee",
                                "content" : JSON.stringify(mes)
                            }
                        ]
                    }
                    console.log(res)
                    fetch(url ,{
                        method: 'POST',
                        headers: {
                            'Content-Type': 'application/x-www-form-urlencoded',
                            'token' : localStorage.token,
                            "Accept" : "text/html",
                        },
                        body : qs.stringify(res),
                        mode : 'cors'
                    }
                    )
                    .then((res) => {
                        if (res.status === 201) {
                        res.text().then((ans) => {
                            console.log(ans)
                            this.$router.push({ name: "Home" });
                        })
                        } else {
                            console.log(res)
                            alert("Something Went Wrong");
                        }
                    })
                    .catch((err) => {
                        console.log(err);
                    });
            } else {
                alert('Email required')
            }
        },
    }
}
</script>

